import React, { ReactNode, useEffect, useState } from 'react';
import { Button } from './Button';

type TProps = {
    alignment?: 'left' | 'center' | 'right';
    children: ReactNode;
    open: boolean;
};

export function Spoiler ({ alignment, children, open }: TProps) {
    const className = 'spoiler';
    const classNames = [className];

    if (alignment) {
        classNames.push(`${className}--alignment-${alignment}`);
    }

    const [show, setShow] = useState(false);

    useEffect(() => {
        setShow(false);
    }, [children]);

    function buttonHandler () {
        setShow(true);
    }

    return (
        <div className={classNames.join(' ')} role='region' aria-live='polite'>
            {
                open || show ? (
                    <div className={`${className}__content`}>
                        {children}
                    </div>
                ) : (
                    <Button
                        callback={buttonHandler}
                        iconName='eye'
                        tooltip='Show'
                    />
                )
            }
        </div>
    );
}