import { ArcElement, Chart, ChartConfiguration, ChartData, DoughnutController, Legend, Tooltip } from 'chart.js';
import isEqual from 'lodash/isEqual';
import React, { useEffect, useRef, useState } from 'react';
import { colors } from '../colors';
import { usePrevious } from '../hooks';

Chart.register(ArcElement, DoughnutController, Legend, Tooltip);

type TProps = {
    data: ChartData<'doughnut'>;
};

export function ChartDoughnut ({ data }: TProps) {
    const className = 'chart-doughnut';

    const [chart, setChart] = useState<Chart<'doughnut'>>();

    const chartRef = useRef<HTMLCanvasElement>(null);
    const previousData: ChartData<'doughnut'> = usePrevious(data);

    Chart.defaults.font.family = 'Roboto';
    Chart.defaults.font.size = 14;
    Chart.defaults.font.weight = 500;

    const config: ChartConfiguration<'doughnut'> = {
        type: 'doughnut',
        options: {
            color: colors.white,
            elements: {
                arc: {
                    borderColor: colors.greyDark,
                    borderRadius: 5,
                    borderWidth: 4
                }
            },
            maintainAspectRatio: false,
            plugins: {
                legend: {
                    labels: {
                        borderRadius: 2,
                        boxHeight: 20,
                        boxWidth: 20,
                        useBorderRadius: true
                    }
                }
            }
        },
        data
    };

    useEffect(() => {
        if (!chartRef.current) {
            return;
        }

        setChart(new Chart(chartRef.current, config));
    }, []);

    useEffect(() => {
        if (!chart) {
            return;
        }

        if (!isEqual(data, previousData)) {
            chart.data = data;
            chart.update();
        }
    }, [data]);

    return (
        <div className={className} aria-hidden='true'>
            <div className={`${className}__chart-container`}>
                <canvas ref={chartRef} />
            </div>
        </div>
    );
}