import React from 'react';
import { TPuzzleName } from '../types';

type TProps = {
    puzzleName: TPuzzleName;
};

export function Orientation ({ puzzleName }: TProps) {
    const className = 'orientation';

    let content;
    switch (puzzleName) {
        case '2x2x2':
        case '3x3x3':
            content = renderNxNxN();
            break;
    }

    function renderNxNxN () {
        return (
            <ul className={`${className}__list`}>
                {/* Up */}
                <li className={`${className}__item`}>
                    <div
                        className={`${className}__color`}
                        style={{ backgroundColor: `var(--color-${puzzleName}-up)` }}
                    />
                    <h3 className={`${className}__title`}>Up</h3>
                </li>
                {/* Front */}
                <li className={`${className}__item`}>
                    <div
                        className={`${className}__color`}
                        style={{ backgroundColor: `var(--color-${puzzleName}-front)` }}
                    />
                    <h3 className={`${className}__title`}>Front</h3>
                </li>
            </ul>
        );
    };

    return (
        <div className={className}>
            {content}
        </div>
    );
}