import React, { useState } from 'react';
import { useEventListener } from '../hooks';
import { Icon } from './Icon';

type TProps = {
    running: boolean;
    startCallback: () => void;
    stopCallback: () => void;
};

export const ButtonTimer = ({ running, startCallback, stopCallback }: TProps) => {
    const className = 'button-timer';
    const classNames = [className];

    const [holding, setHolding] = useState(false);

    useEventListener(document, 'pointerup', buttonUpEventHandler);
    useEventListener(document, 'keydown', keydownHandler);
    useEventListener(document, 'keyup', keyupHandler);

    function keydownHandler (event: KeyboardEvent) {
        if (
            event.altKey
            || event.ctrlKey
            || event.metaKey
            || event.shiftKey
        ) {
            return;
        }

        // Space
        if (event.code === 'Space') {
            event.preventDefault();
            if (!holding) {
                buttonDownEventHandler();
            }
        }
    }

    function keyupHandler (event: KeyboardEvent) {
        if (
            event.altKey
            || event.ctrlKey
            || event.metaKey
            || event.shiftKey
        ) {
            return;
        }

        // Space
        if (event.code === 'Space') {
            event.preventDefault();
            buttonUpEventHandler();
        }
    }

    function buttonDownEventHandler () {
        if (running) {
            stopCallback();
        } else if (!holding) {
            setHolding(true);
        }
    }

    function buttonUpEventHandler () {
        if (holding) {
            setHolding(false);
            startCallback();
        }
    }

    function render () {
        if (holding) {
            classNames.push(`${className}--active`);
        }

        const tooltip = 'Start timer (Space)';

        return (
            <button
                aria-label={tooltip}
                className={classNames.join(' ')}
                data-tooltip={tooltip}
                data-tooltip-position='center'
                onPointerDown={buttonDownEventHandler}
                type='button'
            >
                <Icon name='stopwatch' parentClass={`${className}__icon`} />
            </button>
        );
    }

    return render();
};