import React from 'react';
import { ButtonRound } from '../components/ButtonRound';
import { Group } from '../components/Group';
import { Header } from '../components/Header';
import { Page } from '../components/Page';
import { useEventListener } from '../hooks';

export function PageNotFound () {
    const className = 'page-not-found';

    useEventListener(document, 'keydown', keydownHandler);

    const homeUrl = '/';

    function keydownHandler (event: KeyboardEvent) {
        if (
            event.altKey
            || event.ctrlKey
            || event.metaKey
            || event.shiftKey
        ) {
            return;
        }

        // H
        if (event.code === 'KeyH') {
            event.preventDefault();
            window.location.href = homeUrl;
        }
    }

    function renderHeader () {
        return (
            <Header>
                {/* Home */}
                <ButtonRound
                    href={homeUrl}
                    iconName='house'
                    tooltip='Home (H)'
                />
            </Header>
        );
    }

    return (
        <Page>
            <div className={className}>
                {renderHeader()}
                <img className={`${className}__logo`} src="/assets/images/logo.svg" alt="Logo" />
                <Group alignment='center' title='Error 404'>
                    <h1 className={`${className}__title`}>Page Not Found</h1>
                    <p className={`${className}__text`}>The page you're looking for doesn't exist.</p>
                </Group>
            </div>
        </Page>
    );
}