import React, { Fragment, useEffect, useState } from 'react';
import { Accordion } from '../components/Accordion';
import { Button } from '../components/Button';
import { ButtonRound } from '../components/ButtonRound';
import { Case } from '../components/Case';
import { Group } from '../components/Group';
import { Header } from '../components/Header';
import { List } from '../components/List';
import { Page } from '../components/Page';
import { Separator } from '../components/Separator';
import { useEventListener } from '../hooks';
import { storeManager } from '../StoreManager';
import { TPuzzleName, TTrainer, TTrainerCase, TTrainerCaseStatus, TTrainerSet } from '../types';
import { capitalize, fetchTrainer, getCases, getCasesFromSet, getCasesFromSetSelected, getCasesSelected, getCasesWithStatus, getSets, trainerCaseStatus, updateColorSchemeCustomProperties } from '../utils';

export function Cases () {
    const className = 'cases';

    const [loading, setLoading] = useState(true);
    const [trainer, setTrainer] = useState<TTrainer>();

    const pathname = window.location.pathname.split('/');
    const puzzleName = pathname[2] as TPuzzleName;
    const trainerId = pathname[3];

    if (!puzzleName) {
        throw new Error('Missing puzzle name.');
    }

    if (!trainerId) {
        throw new Error('Missing trainer id.');
    }

    document.title = `SST - Cases ${puzzleName} ${capitalize(trainerId)}`;

    const homeUrl = '/';
    const trainerUrl = `/trainer/${puzzleName}/${trainerId}`;
    const informationUrl = `/trainer/${puzzleName}/${trainerId}/information`;

    useEffect(() => {
        updateTrainer();

        updateColorSchemeCustomProperties(storeManager.getColorScheme(puzzleName));
    }, []);

    useEventListener(document, 'keydown', keydownHandler);

    async function updateTrainer () {
        const newTrainer = await fetchTrainer(puzzleName, trainerId);

        if (!newTrainer) {
            throw new Error('Could not load trainer.');
        }

        setTrainer(newTrainer);
        setLoading(false);

        return;
    }

    function keydownHandler (event: KeyboardEvent) {
        if (
            event.altKey
            || event.ctrlKey
            || event.metaKey
            || event.shiftKey
        ) {
            return;
        }

        // H
        if (event.code === 'KeyH') {
            event.preventDefault();
            window.location.href = homeUrl;
        }

        // T
        if (event.code === 'KeyT') {
            event.preventDefault();
            window.location.href = trainerUrl;
        }

        // I
        if (event.code === 'KeyI') {
            event.preventDefault();
            window.location.href = informationUrl;
        }
    }

    function toggleCaseSelected (caseId: string) {
        if (!caseId) {
            throw new Error('Missing case id.');
        };

        const storeTrainerCase = storeManager.getCase(puzzleName, trainerId, caseId);

        storeManager.setCaseSelected(puzzleName, trainerId, caseId, !storeTrainerCase.selected);

        updateTrainer();
    }

    function changeCaseStatus (caseId: string) {
        if (!caseId) {
            throw new Error('Missing case id.');
        }

        const storeTrainerCase = storeManager.getCase(puzzleName, trainerId, caseId);

        let newStatus: TTrainerCaseStatus = 0;

        switch (storeTrainerCase.status) {
            case 0:
                newStatus = 1;
                break;
            case 1:
                newStatus = 2;
                break;
            case 2:
                newStatus = 0;
                break;
        }

        storeManager.setCaseStatus(puzzleName, trainerId, caseId, newStatus);
        updateTrainer();
    }

    function changeSelectedCasesStatus (selectedCasesId: string[], status: TTrainerCaseStatus) {
        if (!selectedCasesId) {
            throw new Error('Missing selected cases id.');
        }

        if (typeof(status) !== 'number') {
            throw new Error('Missing status.');
        }

        const confirm = window.confirm(
            `Are you sure you want to change the status of all ${selectedCasesId.length} selected cases to "${trainerCaseStatus[status].label}"?`
        );

        if (confirm) {
            storeManager.setCasesStatus(puzzleName, trainerId, selectedCasesId, status);
            updateTrainer();
        }
    }

    function renderHeader () {
        return (
            <Header>
                {/* Home */}
                <ButtonRound
                    href={homeUrl}
                    iconName='house'
                    tooltip='Home (H)'
                />
                {/* Trainer */}
                <ButtonRound
                    href={trainerUrl}
                    iconName='cube'
                    tooltip='Trainer (T)'
                />
                {/* Information */}
                <ButtonRound
                    href={informationUrl}
                    iconName='i-circle'
                    tooltip='Information (I)'
                />
            </Header>
        );
    }

    function renderQuickSelection () {
        if (!trainer) {
            throw new Error('Missing trainer.');
        }

        const trainerCases = getCases(trainer);
        const trainerCasesId = trainerCases.map((setCase) => setCase.id);

        const casesIdWithStatus = [
            getCasesWithStatus(trainer, 0).map((setCase) => setCase.id),
            getCasesWithStatus(trainer, 1).map((setCase) => setCase.id),
            getCasesWithStatus(trainer, 2).map((setCase) => setCase.id)
        ];

        return (
            <Accordion title='Quick Selection'>
                <Group title='Select'>
                    <List>
                        <Button
                            callback={() => {
                                storeManager.setCasesSelected(puzzleName, trainerId, trainerCasesId, true);
                                updateTrainer();
                            }}
                            label='All'
                            iconName='check-square'
                            tooltip='Select all cases'
                        />
                        <Button
                            callback={() => {
                                storeManager.setCasesSelected(puzzleName, trainerId, casesIdWithStatus[0], true);
                                updateTrainer();
                            }}
                            color={trainerCaseStatus[0].color}
                            iconName='check-square'
                            label={`${trainerCaseStatus[0].label}`}
                            tooltip={`Select cases with status "${trainerCaseStatus[0].label}"`}
                        />
                        <Button
                            callback={() => {
                                storeManager.setCasesSelected(puzzleName, trainerId, casesIdWithStatus[1], true);
                                updateTrainer();
                            }}
                            color={trainerCaseStatus[1].color}
                            iconName='check-square'
                            label={`${trainerCaseStatus[1].label}`}
                            tooltip={`Select cases with status "${trainerCaseStatus[1].label}"`}
                        />
                        <Button
                            callback={() => {
                                storeManager.setCasesSelected(puzzleName, trainerId, casesIdWithStatus[2], true);
                                updateTrainer();
                            }}
                            color={trainerCaseStatus[2].color}
                            iconName='check-square'
                            label={`${trainerCaseStatus[2].label}`}
                            tooltip={`Select cases with status "${trainerCaseStatus[2].label}"`}
                        />
                    </List>
                </Group>
                <Group title='Deselect'>
                    <List>
                        <Button
                            callback={() => {
                                storeManager.setCasesSelected(puzzleName, trainerId, trainerCasesId, false);
                                updateTrainer();
                            }}
                            label='All'
                            iconName='square'
                            tooltip='Deselect all cases'
                        />
                        <Button
                            callback={() => {
                                storeManager.setCasesSelected(puzzleName, trainerId, casesIdWithStatus[0], false);
                                updateTrainer();
                            }}
                            color={trainerCaseStatus[0].color}
                            iconName='square'
                            label={`${trainerCaseStatus[0].label}`}
                            tooltip={`Deselect cases with status "${trainerCaseStatus[0].label}"`}
                        />
                        <Button
                            callback={() => {
                                storeManager.setCasesSelected(puzzleName, trainerId, casesIdWithStatus[1], false);
                                updateTrainer();
                            }}
                            color={trainerCaseStatus[1].color}
                            iconName='square'
                            label={`${trainerCaseStatus[1].label}`}
                            tooltip={`Deselect cases with status "${trainerCaseStatus[1].label}"`}
                        />
                        <Button
                            callback={() => {
                                storeManager.setCasesSelected(puzzleName, trainerId, casesIdWithStatus[2], false);
                                updateTrainer();
                            }}
                            color={trainerCaseStatus[2].color}
                            iconName='square'
                            label={`${trainerCaseStatus[2].label}`}
                            tooltip={`Deselect cases with status "${trainerCaseStatus[2].label}"`}
                        />
                    </List>
                </Group>
            </Accordion>
        );
    }

    function renderAll () {
        if (!trainer) {
            throw new Error('Missing trainer.');
        }

        const trainerCases = getCases(trainer);
        const trainerCasesId = trainerCases.map((setCase) => setCase.id);
        const selectedCases = getCasesSelected(trainer);

        return (
            <Accordion
                anchorId='all'
                title={`All (${selectedCases.length}/${trainerCases.length})`}
            >
                <List>
                    <Button
                        callback={() => {
                            storeManager.setCasesSelected(puzzleName, trainerId, trainerCasesId, true);
                            updateTrainer();
                        }}
                        iconName='check-square'
                        label='Select All'
                        tooltip='Select all cases'


                    />
                    <Button
                        callback={() => {
                            storeManager.setCasesSelected(puzzleName, trainerId, trainerCasesId, false);
                            updateTrainer();
                        }}
                        iconName='square'
                        label='Deselect All'
                        tooltip='Deselect all cases'


                    />
                </List>
                <ul className={`${className}__list`} aria-multiselectable='true'>
                    {
                        trainerCases.map((trainerCase) => (
                            <li
                                className={`${className}__item`}
                                key={`case-${trainerCase.id}`}
                            >
                                {renderCase(trainerCase)}
                            </li>
                        ))
                    }
                </ul>
            </Accordion>
        );
    }

    function renderSets () {
        if (!trainer) {
            throw new Error('Missing trainer.');
        }

        return Object.keys(trainer.sets).length > 1 && (
            <Fragment>
                <div className={`${className}__accordions`}>
                    {
                        getSets(trainer).map((trainerSet) => renderSet(trainerSet))
                    }
                </div>
                <Separator />
            </Fragment>
        );
    }

    function renderSet (trainerSet: TTrainerSet) {
        if (!trainer) {
            throw new Error('Missing trainer.');
        }

        if (!trainerSet) {
            throw new Error('Missing trainer set.');
        }

        const setCases = getCasesFromSet(trainer, trainerSet.id);
        const setCasesId = setCases.map((setCase) => setCase.id);
        const selectedSetCases = getCasesFromSetSelected(trainer, trainerSet.id);

        return (
            <Accordion
                anchorId={`set-${trainerSet.id}`}
                key={`group-${trainerSet.id}`}
                title={`${trainer.sets[trainerSet.id].name} (${selectedSetCases.length}/${setCases.length})`}
            >
                <List>
                    <Button
                        callback={() => {
                            storeManager.setCasesSelected(puzzleName, trainerId, setCasesId, true);
                            updateTrainer();
                        }}
                        label='Select Set'
                        iconName='check-square'
                        tooltip='Select cases from set'
                    />
                    <Button
                        callback={() => {
                            storeManager.setCasesSelected(puzzleName, trainerId, setCasesId, false);
                            updateTrainer();
                        }}
                        label='Deselect Set'
                        iconName='square'
                        tooltip='Deselect cases from set'
                    />
                </List>
                <ul className={`${className}__list`} aria-multiselectable='true'>
                    {
                        setCases.map((setCase) => (
                            <li
                                className={`${className}__item`}
                                key={`item-${setCase.id}`}
                            >
                                {renderCase(setCase)}
                            </li>
                        ))
                    }
                </ul>
            </Accordion>
        );
    }

    function renderCase (trainerCase: TTrainerCase) {
        if (!trainerCase) {
            throw new Error('Missing trainer case.');
        }

        return (
            <Case
                puzzleName={puzzleName}
                selectionCallback={() => toggleCaseSelected(trainerCase.id)}
                statusCallback={() => changeCaseStatus(trainerCase.id)}
                trainerCase={trainerCase}
                trainerId={trainerId}
            />
        );
    }

    function renderQuickStatusChange () {
        if (!trainer) {
            throw new Error('Missing trainer.');
        }

        const selectedCases = getCasesSelected(trainer);
        const selectedCasesId = selectedCases.map((trainerCase) => trainerCase.id);

        return (
            <Accordion title='Quick Status Change'>
                <p className={`${className}__description`}>Apply the following status to all selected cases</p>
                <List>
                    <Button
                        ariaLabel={`Change selected cases status to "${trainerCaseStatus[0].label}"`}
                        callback={() => changeSelectedCasesStatus(selectedCasesId, 0)}
                        color={trainerCaseStatus[0].color}
                        iconName={trainerCaseStatus[0].iconName}
                        label={trainerCaseStatus[0].label}
                    />
                    <Button
                        ariaLabel={`Change selected cases status to "${trainerCaseStatus[1].label}"`}
                        callback={() => changeSelectedCasesStatus(selectedCasesId, 1)}
                        color={trainerCaseStatus[1].color}
                        iconName={trainerCaseStatus[1].iconName}
                        label={trainerCaseStatus[1].label}
                    />
                    <Button
                        ariaLabel={`Change selected cases status to "${trainerCaseStatus[2].label}"`}
                        callback={() => changeSelectedCasesStatus(selectedCasesId, 2)}
                        color={trainerCaseStatus[2].color}
                        iconName={trainerCaseStatus[2].iconName}
                        label={trainerCaseStatus[2].label}
                    />
                </List>
            </Accordion>
        );
    }

    return (
        <Page loading={loading} title={`Cases - ${trainer?.name}`}>
            {
                trainer && (
                    <div className={className}>
                        {/* Header */}
                        {renderHeader()}
                        <div className={`${className}__content`}>
                            {renderQuickSelection()}
                            <Separator />
                            {renderAll()}
                            <Separator />
                            {renderSets()}
                            {renderQuickStatusChange()}
                        </div>
                    </div>
                )
            }
        </Page>
    );
}