import { ChartData } from 'chart.js';
import React, { useEffect, useState } from 'react';
import { colors } from '../colors';
import { Button } from '../components/Button';
import { ButtonRound } from '../components/ButtonRound';
import { ChartBar } from '../components/ChartBar';
import { ChartDoughnut } from '../components/ChartDoughnut';
import { Group } from '../components/Group';
import { Header } from '../components/Header';
import { Info } from '../components/Info';
import { List } from '../components/List';
import { Page } from '../components/Page';
import { useEventListener } from '../hooks';
import { TPuzzleName, TTrainer } from '../types';
import { capitalize, fetchTrainer, getAverageTime, getCases, getCasesWithStatus, getCompletionPercentage, trainerCaseStatus } from '../utils';

export function Information () {
    const className = 'information';

    const [loading, setLoading] = useState(true);
    const [trainer, setTrainer] = useState<TTrainer>();
    const [sortType, setSortType] = useState<'default'| 'ascending' | 'descending'>('default');

    const pathname = window.location.pathname.split('/');
    const puzzleName = pathname[2] as TPuzzleName;
    const trainerId = pathname[3];

    if (!puzzleName) {
        throw new Error('Missing puzzle name.');
    }

    if (!trainerId) {
        throw new Error('Missing trainer id.');
    }

    document.title = `SST - Information ${puzzleName} ${capitalize(trainerId)}`;

    const homeUrl = '/';
    const trainerUrl = `/trainer/${puzzleName}/${trainerId}`;
    const casesUrl = `/trainer/${puzzleName}/${trainerId}/cases`;

    useEffect(() => {
        updateTrainer();
    }, []);

    useEventListener(document, 'keydown', keydownHandler);

    async function updateTrainer () {
        const newTrainer = await fetchTrainer(puzzleName, trainerId);

        if (!newTrainer) {
            throw new Error('Could not load trainer.');
        }

        setTrainer(newTrainer);
        setLoading(false);

        return;
    }

    function keydownHandler (event: KeyboardEvent) {
        if (
            event.altKey
            || event.ctrlKey
            || event.metaKey
            || event.shiftKey
        ) {
            return;
        }

        // H
        if (event.code === 'KeyH') {
            event.preventDefault();
            window.location.href = homeUrl;
        }

        // C
        if (event.code === 'KeyC') {
            event.preventDefault();
            window.location.href = casesUrl;
        }

        // T
        if (event.code === 'KeyT') {
            event.preventDefault();
            window.location.href = trainerUrl;
        }
    }

    function renderHeader () {
        return (
            <Header>
                {/* Home */}
                <ButtonRound
                    href={homeUrl}
                    iconName='house'
                    tooltip='Home (H)'
                />
                {/* Cases */}
                <ButtonRound
                    href={casesUrl}
                    iconName='rectangles'
                    tooltip='Cases (C)'
                />
                {/* Trainer */}
                <ButtonRound
                    href={trainerUrl}
                    iconName='cube'
                    tooltip='Trainer (T)'
                />
            </Header>
        );
    }

    function renderInformation () {
        if (!trainer) {
            throw new Error('No trainer.');
        }

        const numberOfSets = Object.keys(trainer.sets).length;
        const numberOfCases = Object.keys(trainer.cases).length;

        return (
            <Group
                alignment='left'
                title='Information'
                tooltip='Information about the trainer'
            >
                <List gap={15}>
                    <Info label='Name' value={trainer.name} />
                    <Info label='Method' value={trainer.method} />
                    <Info label='Version' value={trainer.version.toString()} />
                    <Info label='Author(s)' value={trainer.authors} />
                </List>
                <Info direction='column' label='Description' value={trainer.description} />
                <List gap={15}>
                    <Info label='Number of sets' value={numberOfSets.toString()} />
                    <Info label='Number of cases' value={numberOfCases.toString()} />
                </List>
            </Group>
        );
    }

    function renderCompletion () {
        if (!trainer) {
            throw new Error('No trainer.');
        }

        const numberOfCases = Object.keys(trainer.cases).length;

        const numberOfCasesWithStatus = [
            getCasesWithStatus(trainer, 0).length,
            getCasesWithStatus(trainer, 1).length,
            getCasesWithStatus(trainer, 2).length
        ];

        const complettionPercentage = getCompletionPercentage(trainer);

        const data: ChartData<'doughnut'> = {
            labels: [
                trainerCaseStatus[2].label,
                trainerCaseStatus[1].label,
                trainerCaseStatus[0].label
            ],
            datasets: [
                {
                    data: [
                        numberOfCasesWithStatus[2],
                        numberOfCasesWithStatus[1],
                        numberOfCasesWithStatus[0]
                    ],
                    backgroundColor: [
                        colors.green,
                        colors.orange,
                        colors.red
                    ]
                }
            ]
        };

        return (
            <Group
                alignment='left'
                title='Completion'
                tooltip='Completion of the trainer'
            >
                <List gap={15}>
                    {/* Total */}
                    <Info
                        label='Total'
                        value={numberOfCases.toString()}
                    />
                    {/* Status 2 */}
                    <Info
                        color={trainerCaseStatus[2].color}
                        label={capitalize(trainerCaseStatus[2].label)}
                        value={numberOfCasesWithStatus[2].toString()}
                    />
                    {/* Status 1 */}
                    <Info
                        color={trainerCaseStatus[1].color}
                        label={capitalize(trainerCaseStatus[1].label)}
                        value={numberOfCasesWithStatus[1].toString()}
                    />
                    {/* Status 0 */}
                    <Info
                        color={trainerCaseStatus[0].color}
                        label={capitalize(trainerCaseStatus[0].label)}
                        value={numberOfCasesWithStatus[0].toString()}
                    />
                    {/* Completion */}
                    <Info
                        label='Completion'
                        value={`${complettionPercentage}%`}
                    />
                </List>
                <ChartDoughnut data={data} />
            </Group>
        );
    }

    function renderAverages () {
        if (!trainer) {
            throw new Error('No trainer.');
        }

        const casesData: { label: string; average: number; }[] = [];
        const labels: string[] = [];
        const averages: number[] = [];

        const cases = getCases(trainer);

        cases.forEach((trainerCase) => {
            const { name, times } = trainerCase;

            let average = 0;

            if (times && times.length > 0) {
                average = getAverageTime(times) || 0;
            }

            const caseData = {
                label: name,
                average
            };

            casesData.push(caseData);
        });

        if (sortType === 'ascending') {
            casesData.sort((a, b) => {
                if (a.average < b.average) {
                    return -1;
                } else if (a.average > b.average) {
                    return 1;
                }

                return 0;
            });
        }

        if (sortType === 'descending') {
            casesData.sort((a, b) => {
                if (a.average > b.average) {
                    return -1;
                } else if (a.average < b.average) {
                    return 1;
                }

                return 0;
            });
        }

        casesData.forEach((caseData) => {
            labels.push(caseData.label);
            averages.push(caseData.average);
        });

        const data: ChartData<'bar'> = {
            labels,
            datasets: [
                {
                    backgroundColor: colors.blue,
                    data: averages,
                    label: 'Average'
                }
            ]
        };

        return (
            <Group
                title='Averages'
                tooltip='Average time for each case'
            >
                <List gap={15}>
                    <Button
                        callback={() => setSortType('default')}
                        label='Default'
                        tooltip='Sort by default'
                    />
                    <Button
                        callback={() => setSortType('ascending')}
                        iconName='lines-longer'
                        label='Ascending'
                        tooltip='Sort from fastest to slowest'
                    />
                    <Button
                        callback={() => setSortType('descending')}
                        iconName='lines-shorter'
                        label='Descending'
                        tooltip='Sort from slowest to fastest'
                    />
                </List>
                <ChartBar data={data} />
            </Group>
        );
    }

    return (
        <Page loading={loading} title={`Information - ${trainer?.name}`}>
            {
                trainer && (
                    <div className={className}>
                        {/* Header */}
                        {renderHeader()}
                        <div className={`${className}__content`}>
                            {/* Information */}
                            {renderInformation()}
                            {/* Completion */}
                            {renderCompletion()}
                            {/* Averages */}
                            {renderAverages()}
                        </div>
                    </div>
                )
            }
        </Page>
    );
}