import { BarController, BarElement, CategoryScale, Chart, ChartConfiguration, ChartData, Legend, LinearScale, Tooltip } from 'chart.js';
import isEqual from 'lodash/isEqual';
import React, { useEffect, useRef, useState } from 'react';
import { colors } from '../colors';
import { usePrevious } from '../hooks';
import { formatTime } from '../utils';

Chart.register(BarController, BarElement, CategoryScale, Legend, LinearScale, Tooltip);

type TProps = {
    data: ChartData<'bar'>;
};

export function ChartBar ({ data }: TProps) {
    const className = 'chart-bar';

    const [chart, setChart] = useState<Chart<'bar'>>();

    const chartRef = useRef<HTMLCanvasElement>(null);
    const previousData: ChartData<'bar'> = usePrevious(data);

    Chart.defaults.font.family = 'Roboto';
    Chart.defaults.font.size = 14;
    Chart.defaults.font.weight = 500;

    const config: ChartConfiguration<'bar'> = {
        type: 'bar',
        options: {
            color: colors.white,
            elements: {
                bar: {
                    borderRadius: 2
                }
            },
            indexAxis: 'y',
            maintainAspectRatio: false,
            plugins: {
                legend: {
                    labels: {
                        borderRadius: 2,
                        boxHeight: 20,
                        boxWidth: 20,
                        useBorderRadius: true
                    }
                },
                tooltip: {
                    callbacks: {
                        label: (context) => formatTime(Number(context.raw))
                    }
                }
            },
            scales: {
                x: {
                    grid: {
                        color: colors.grey
                    },
                    ticks: {
                        callback: (value) => formatTime(Number(value)),
                        color: colors.white
                    }
                },
                y: {
                    grid: {
                        color: colors.grey
                    },
                    ticks: {
                        color: colors.white
                    }
                }
            }
        },
        data
    };

    useEffect(() => {
        if (!chartRef.current) {
            return;
        }

        setChart(new Chart(chartRef.current, config));
    }, []);

    useEffect(() => {
        if (!chart) {
            return;
        }

        if (!isEqual(data, previousData)) {
            chart.data = data;
            chart.update();
        }
    }, [data]);

    return (
        <div className={className} aria-hidden='true'>
            <div
                className={`${className}__chart-container`}
                style={{ height: data.datasets[0].data.length * 30 + 80 }}
            >
                <canvas ref={chartRef} />
            </div>
        </div>
    );
}