import React from 'react';

type TProps = {
    color?: 'blue' | 'green' | 'grey' | 'orange' | 'red' | 'yellow';
    direction?: 'row' | 'column';
    label: string;
    value: string;
};

export function Info ({ color = 'grey', direction = 'row', label, value }: TProps) {
    const className = 'info';
    const classNames = [className];

    classNames.push(`${className}--direction-${direction}`);
    classNames.push(`${className}--color-${color}`);

    return (
        <p className={classNames.join(' ')}>
            <span className={`${className}__label`}>{label}</span>
            <span className={`${className}__value`}>{value}</span>
        </p>
    );
}