import React from 'react';
import { Link } from 'react-router-dom';
import { Accordion } from '../components/Accordion';
import { ButtonRound } from '../components/ButtonRound';
import { Header } from '../components/Header';
import { Page } from '../components/Page';
import { useEventListener } from '../hooks';

export function Faq () {
    const className = 'faq';

    document.title = 'SST - FAQ';

    const homeUrl = '/';

    useEventListener(document, 'keydown', keydownHandler);

    function keydownHandler (event: KeyboardEvent) {
        if (
            event.altKey
            || event.ctrlKey
            || event.metaKey
            || event.shiftKey
        ) {
            return;
        }

        // H
        if (event.code === 'KeyH') {
            event.preventDefault();
            window.location.href = homeUrl;
        }
    }

    function renderHeader () {
        return (
            <Header>
                {/* Home */}
                <ButtonRound
                    href={homeUrl}
                    iconName='house'
                    tooltip='Home (H)'
                />
            </Header>
        );
    }

    return (
        <Page title='FAQ'>
            <div className={className}>
                {/* Header */}
                {renderHeader()}
                <div className={`${className}__content`}>
                    <Accordion title='What is Speed Solving Trainer?'>
                        <div className={`${className}__texts`}>
                            <p className={`${className}__text`}>Speed Solving Trainer (SST) is a free web application created to help you practice solving your favorite puzzles.</p>
                            <p className={`${className}__text`}>It can be used on any device with a web browser (desktops, laptops, tablets, smartphones etc) and doesn't require an account.</p>
                        </div>
                    </Accordion>
                    <Accordion title='Does the app collect personal information?'>
                        <div className={`${className}__texts`}>
                            <p className={`${className}__text`}>No, it does not collect any personal information.</p>
                            <p className={`${className}__text`}>The app uses <a className={`${className}__link`} href='https://www.goatcounter.com' target='_blank'>GoatCounter</a> which is a privacy focused analytics solution.</p>
                        </div>
                    </Accordion>
                    <Accordion title='I have a question/an issue, where can I get help?'>
                        <div className={`${className}__texts`}>
                            <p className={`${className}__text`}>The simplest way is to join our <a className={`${className}__link`} href='https://discord.gg/F6eaxK88sx' target='_blank'>Discord server</a>.</p>
                            <p className={`${className}__text`}>There, you will find dedicated channels to receive help, report a bug etc.</p>
                            <p className={`${className}__text`}>Note: SST is a passion project developed by one person, it may take some time to get an answer or a bug fixed.</p>
                        </div>
                    </Accordion>
                    <Accordion title={'Where is the app\'s data stored?'}>
                        <div className={`${className}__texts`}>
                            <p className={`${className}__text`}>The app stores data in your browser's memory, on your device.</p>
                            <p className={`${className}__text`}>Note: Clearing your browser's memory will also erase the app's data.</p>
                        </div>
                    </Accordion>
                    <Accordion title='How can I use the app on a different device?'>
                        <div className={`${className}__texts`}>
                            <p className={`${className}__text`}>On your current device:</p>
                            <p className={`${className}__text`}>1) Go to the <Link className={`${className}__link`} to='/settings' target='_blank'>settings</Link>.</p>
                            <p className={`${className}__text`}>2) In the "Backups" section, click the "Export" button.</p>
                            <p className={`${className}__text`}>3) Save the file somewhere your new device can access it.</p>
                            <p className={`${className}__text`}>On your new device:</p>
                            <p className={`${className}__text`}>1) Go to the <Link className={`${className}__link`} to='/settings' target='_blank'>settings</Link>.</p>
                            <p className={`${className}__text`}>2) In the "Backups" section, click the "Import" button.</p>
                            <p className={`${className}__text`}>3) Select the file you just saved.</p>
                        </div>
                    </Accordion>
                    <Accordion title='Does the app have keyboard shortcuts?'>
                        <div className={`${className}__texts`}>
                            <p className={`${className}__text`}>Yes! Most buttons in the app have one.</p>
                            <p className={`${className}__text`}>If a button has a keyboard shortcuts, it will appear in parentheses when you hover the button using your mouse.</p>
                        </div>
                    </Accordion>
                    <Accordion title='How can I reset the entire app?'>
                        <div className={`${className}__texts`}>
                            <p className={`${className}__text`}>1) Go to the <Link className={`${className}__link`} to='/settings' target='_blank'>settings</Link>.</p>
                            <p className={`${className}__text`}>2) In the "Danger zone" section, click the "Reset app" button.</p>
                            <p className={`${className}__text`}>3) Confirm.</p>
                            <p className={`${className}__text`}>Note: This is permanent, it cannot be undone.</p>
                        </div>
                    </Accordion>
                </div>
            </div>
        </Page>
    );
}