import React from 'react';
import { TPuzzleName, TTrainerCase } from '../types';
import { formatTime, getAverageTime, getCaseImageSource, trainerCaseStatus } from '../utils';
import { Button } from './Button';
import { Group } from './Group';
import { PuzzlePreview } from './PuzzlePreview';
import { Tag } from './Tag';

type TProps = {
    puzzleName: TPuzzleName;
    selectionCallback: (...args: unknown[]) => unknown;
    statusCallback: (...args: unknown[]) => unknown;
    trainerCase: TTrainerCase;
    trainerId: string;
};

export function Case ({ puzzleName, selectionCallback, statusCallback, trainerCase, trainerId }: TProps) {
    const { favoriteSolution, id, name, selected, solutions, status, times } = trainerCase;

    const className = 'case';
    const classNames = [className];

    if (selected) {
        classNames.push(`${className}--selected`);
    }

    function renderPuzzlePreview () {
        return (
            <div className={`${className}__puzzle-preview`}>
                <PuzzlePreview
                    source={getCaseImageSource(puzzleName, trainerId, id)}
                />
            </div>
        );
    }

    function renderStatus () {
        if (typeof(status) !== 'number') {
            return;
        }

        let tooltip: string;

        switch (status) {
            case 0:
                tooltip = `Change status to "${trainerCaseStatus[1].label}"`;
                break;
            case 1:
                tooltip = `Change status to "${trainerCaseStatus[2].label}"`;
                break;
            case 2:
                tooltip = `Change status to "${trainerCaseStatus[0].label}"`;
                break;
        }

        return (
            <Group alignment='center' title='Status'>
                <Button
                    callback={statusCallback}
                    color={trainerCaseStatus[status].color}
                    label={trainerCaseStatus[status].label}
                    size='small'
                    tooltip={tooltip}
                    tooltipPosition='center'
                />
            </Group>
        );
    }

    function renderSelection () {
        const color = selected ? 'grey-super-light' : 'grey';
        const label = selected ? 'Selected' : 'Not Selected';
        const tooltip = selected ? 'Deselect case' : 'Select case';

        return (
            <Group alignment='center' title='Selection'>
                <Button
                    callback={selectionCallback}
                    color={color}
                    label={label}
                    size='small'
                    tooltip={tooltip}
                    tooltipPosition='center'
                />
            </Group>
        );
    }

    function renderAverage () {
        if (!times || times.length === 0) {
            return null;
        }

        const average = getAverageTime(times);

        if (!average) {
            return null;
        }

        return (
            <Group alignment='center' title='Average'>
                <Tag
                    color='blue'
                    label={formatTime(average)}
                    tooltip='Average time'
                    tooltipPosition='center'
                />
            </Group>
        );
    }

    function renderSolutions () {
        let numberOfSolutions = solutions?.length || 0;

        if (favoriteSolution && !solutions?.includes(favoriteSolution)) {
            numberOfSolutions++;
        }

        if (numberOfSolutions === 0) {
            return null;
        }

        let solutionsTitle: string;
        if (numberOfSolutions > 1) {
            solutionsTitle = `Solutions (${numberOfSolutions})`;
        } else {
            solutionsTitle = `Solution (${numberOfSolutions})`;
        }

        const favoriteSolutionElement = favoriteSolution && (
            <li className={`${className}__solution`}>
                <Tag
                    color='grey-super-light'
                    label={favoriteSolution}
                    largeLetterSpacing={true}
                    size='large'
                    tooltip='Favorite solution'
                    tooltipPosition='center'
                />
            </li>
        );

        const solutionElements = solutions?.map((solution, index) => {
            if (solution !== favoriteSolution) {
                return (
                    <li className={`${className}__solution`} key={`solution-${index}`}>
                        <Tag
                            color='grey'
                            label={solution}
                            largeLetterSpacing={true}
                            size='large'
                        />
                    </li>
                );
            }
        });

        return (
            <Group title={solutionsTitle}>
                <ul className={`${className}__solutions`}>
                    {favoriteSolutionElement}
                    {solutionElements}
                </ul>
            </Group>
        );
    }

    return (
        <Group title={name}>
            <div className={classNames.join(' ')}>
                {renderPuzzlePreview()}
                <div className={`${className}__content`}>
                    <div className={`${className}__info`}>
                        {renderStatus()}
                        {renderSelection()}
                        {renderAverage()}
                    </div>
                    {renderSolutions()}
                </div>
            </div>
        </Group>
    );
}