import { ChartData } from 'chart.js';
import React, { Fragment, ReactNode, useEffect, useRef, useState } from 'react';
import { colors } from '../colors';
import { Accordion } from '../components/Accordion';
import { Button } from '../components/Button';
import { ButtonRound } from '../components/ButtonRound';
import { ButtonStatus } from '../components/ButtonStatus';
import { ButtonTimer } from '../components/ButtonTimer';
import { ChartLine } from '../components/ChartLine';
import { Group } from '../components/Group';
import { Header } from '../components/Header';
import { Info } from '../components/Info';
import { List } from '../components/List';
import { Orientation } from '../components/Orientation';
import { Page } from '../components/Page';
import { PuzzlePreview } from '../components/PuzzlePreview';
import { Spoiler } from '../components/Spoiler';
import { Tag } from '../components/Tag';
import { Time } from '../components/Time';
import { useEventListener } from '../hooks';
import { getColorNeutralColorScheme } from '../puzzles/puzzles';
import { storeManager } from '../StoreManager';
import { TPuzzleName, TTrainer, TTrainerCase, TTrainerCaseStatus, TTrainerCaseTime, TTrainerCaseTimePenaly } from '../types';
import { fetchTrainer, formatTime, getAverageTime, getBestTime, getCaseImageSource, getCasesSelected, getDnfTimes, getPlusTwoTimes, getWorstTime, parseTime, shuffleArray, trainerCaseStatus, updateColorSchemeCustomProperties } from '../utils';

export function Trainer () {
    const className = 'trainer';

    const [activeCaseIndex, setActiveCaseIndex] = useState(0);
    const [loading, setLoading] = useState(true);
    const [milliseconds, setMilliseconds] = useState(0);
    const [running, setRunning] = useState(false);
    const [trainer, setTrainer] = useState<TTrainer>();
    const [trainerCases, setTrainerCases] = useState<TTrainerCase[]>([]);

    const intervalRef = useRef<any>(null);
    const timerRef = useRef<HTMLButtonElement>(null);

    const pathname = window.location.pathname.split('/');
    const puzzleName = pathname[2] as TPuzzleName;
    const trainerId = pathname[3];

    if (!puzzleName) {
        throw new Error('Missing puzzle name.');
    }

    if (!trainerId) {
        throw new Error('Missing trainer id.');
    }

    document.title = `SST - Trainer ${puzzleName} ${trainer?.name}`;

    const homeUrl = '/';
    const casesUrl = `/trainer/${puzzleName}/${trainerId}/cases`;
    const informationUrl = `/trainer/${puzzleName}/${trainerId}/information`;
    const settingsUrl = `/trainer/${puzzleName}/${trainerId}/settings`;

    const settings = storeManager.getSettings();

    let activeCase: TTrainerCase | null = null;
    if (trainerCases && trainerCases.length !== 0) {
        activeCase = trainerCases[activeCaseIndex];
    }

    useEffect(() => {
        updateTrainerCases();

        updateColorSchemeCustomProperties(storeManager.getColorScheme(puzzleName));
    }, []);

    useEffect(() => {
        updatePuzzleColorScheme();
    }, [activeCaseIndex]);

    useEventListener(document, 'keydown', keydownHandler);

    async function updateTrainerCases () {
        const newTrainer = await fetchTrainer(puzzleName, trainerId);

        if (!newTrainer) {
            throw new Error('Could not load trainer.');
        }

        setLoading(false);
        setTrainer(newTrainer);

        const newSelectedCases = getCasesSelected(newTrainer);

        if (settings.trainer.randomizeCaseOrder) {
            setTrainerCases(shuffleArray(newSelectedCases));
        } else {
            setTrainerCases(newSelectedCases);
        }

        return;
    }

    function keydownHandler (event: KeyboardEvent) {
        if (
            event.altKey
            || event.ctrlKey
            || event.metaKey
            || event.shiftKey
        ) {
            return;
        }

        const times = activeCase?.times;

        // H
        if (event.code === 'KeyH') {
            event.preventDefault();
            window.location.href = homeUrl;
        }

        // C
        if (event.code === 'KeyC') {
            event.preventDefault();
            window.location.href = casesUrl;
        }

        // I
        if (event.code === 'KeyI') {
            event.preventDefault();
            window.location.href = informationUrl;
        }

        // S
        if (event.code === 'KeyS') {
            event.preventDefault();
            window.location.href = settingsUrl;
        }

        // F
        if (event.code === 'KeyF') {
            event.preventDefault();
            setFavoriteSolution();
        }

        // A
        if (event.code === 'KeyA') {
            event.preventDefault();
            addTimeManually();
        }

        // Delete
        if (event.code === 'Delete' && times && times.length > 0) {
            event.preventDefault();
            removeTime(times.length -1);
        }

        // P
        if (event.code === 'KeyP' && times && times.length > 0) {
            event.preventDefault();

            const index = times.length - 1;

            switch (times[index].penalty) {
                case '+2':
                    setTimePenalty(index, 'dnf');
                    break;
                case 'dnf':
                    setTimePenalty(index, null);
                    break;
                default:
                    setTimePenalty(index, '+2');
                    break;
            }
        }

        // Left Arrow
        if (event.code === 'ArrowLeft') {
            event.preventDefault();
            previousCase();
        }

        // E
        if (event.code === 'KeyE') {
            event.preventDefault();
            changeStatus();
        }

        // Right Arrow
        if (event.code === 'ArrowRight') {
            event.preventDefault();
            nextCase();
        }
    }

    function updatePuzzleColorScheme () {
        if (settings.puzzles[puzzleName].colorNeutral) {
            const colorScheme = getColorNeutralColorScheme(puzzleName);

            updateColorSchemeCustomProperties(colorScheme);
        }
    }

    function changeStatus () {
        if (!activeCase) {
            throw new Error('Missing active case.');
        }

        const storeTrainerCase = storeManager.getCase(puzzleName, trainerId, activeCase.id);

        let newStatus: TTrainerCaseStatus = 0;

        switch (storeTrainerCase.status) {
            case 0:
                newStatus = 1;
                break;
            case 1:
                newStatus = 2;
                break;
            case 2:
                newStatus = 0;
                break;
        }

        storeManager.setCaseStatus(puzzleName, trainerId, activeCase.id, newStatus);

        updateTrainerCases();
    }

    function setFavoriteSolution () {
        if (!activeCase) {
            throw new Error('Missing active case.');
        }

        const newFavoriteSolution = window.prompt('Enter your favorite solution');

        if (newFavoriteSolution === null) {
            return;
        }

        storeManager.setCaseFavoriteSolution(puzzleName, trainerId, activeCase.id, newFavoriteSolution);

        updateTrainerCases();
    }

    function previousCase () {
        if (!trainerCases || trainerCases.length === 0) {
            throw new Error('Missing trainer cases.');
        }

        if (activeCaseIndex > 0) {
            // Decrease active case index by 1
            setActiveCaseIndex(activeCaseIndex - 1);
        } else {
            // Set active case index to the last index
            setActiveCaseIndex(trainerCases.length - 1);
        }
    }

    function nextCase () {
        if (!trainerCases || trainerCases.length === 0) {
            throw new Error('Missing trainer cases.');
        }

        if (activeCaseIndex < trainerCases.length - 1) {
            // Increase active case index by 1
            setActiveCaseIndex(activeCaseIndex + 1);
        } else {
            // Set active case index to 0
            setActiveCaseIndex(0);
        }
    }

    function startTimer () {
        if (running || milliseconds !== 0) {
            return;
        }

        setRunning(true);

        const start = Date.now();

        intervalRef.current = setInterval(() => {
            // Milliseconds elapsed since start
            const delta = Date.now() - start;

            setMilliseconds(delta);
        }, 10);

        timerRef.current?.focus();
    }

    function stopTimer () {
        clearInterval(intervalRef.current);
        addTime(milliseconds);
        setRunning(false);
        setMilliseconds(0);
    }

    function addTime (milliseconds: number) {
        if (!activeCase) {
            throw new Error('Missing active case.');
        }

        storeManager.addCaseTime(puzzleName, trainerId, activeCase.id, milliseconds);

        updateTrainerCases();
    }

    function addTimeManually () {
        if (!activeCase) {
            throw new Error('Missing active case.');
        }

        let promptText = 'Input your time as an integer (under 24h)\n';
        promptText += '100 = 0.100\n';
        promptText += '1000 = 1.000\n';
        promptText += '100000 = 1:00.000\n';
        promptText += '10000000 = 1:00:00.000\n';

        const prompt = window.prompt(promptText);

        if (prompt === null) {
            return;
        }

        const milliseconds = parseTime(prompt);

        if (milliseconds === null) {
            window.alert('Incorrect time format.');
            return;
        }

        storeManager.addCaseTime(puzzleName, trainerId, activeCase.id, milliseconds);

        updateTrainerCases();
    }

    function setTimePenalty (index: number, penalty: TTrainerCaseTimePenaly) {
        if (typeof index !== 'number') {
            throw new Error('Missing index.');
        }

        if (penalty !== null && typeof penalty !== 'string') {
            throw new Error('Missing penalty.');
        }

        if (!activeCase) {
            throw new Error('Missing active case.');
        }

        storeManager.setCaseTimePenalty(puzzleName, trainerId, activeCase.id, index, penalty);

        updateTrainerCases();
    }

    function removeTime (index: number) {
        if (typeof index !== 'number') {
            throw new Error('Missing index.');
        }

        if (!activeCase) {
            throw new Error('Missing active case.');
        }

        const times = activeCase.times;

        if (!times) {
            throw new Error('Missing times.');
        }

        const time = times[index];

        if (!time) {
            throw new Error('Missing time.');
        }

        const confirm = window.confirm(`Are you sure you want to remove time ${index + 1} (${formatTime(time.milliseconds)})?`);

        if (!confirm) {
            return;
        }

        storeManager.removeCaseTime(puzzleName, trainerId, activeCase.id, index);

        updateTrainerCases();
    }

    function removeAllTimes () {
        if (!activeCase) {
            throw new Error('Missing active case.');
        }

        const confirm = window.confirm('Are you sure you want to deleted all the times for this case?');

        if (!confirm) {
            return;
        }

        storeManager.removeAllCaseTimes(puzzleName, trainerId, activeCase.id);

        updateTrainerCases();
    }

    function renderHeader () {
        return (
            <Header marginLeftIndex={3}>
                {/* Home */}
                <ButtonRound
                    href={homeUrl}
                    iconName='house'
                    tooltip='Home (H)'
                />
                {/* Cases */}
                <ButtonRound
                    href={casesUrl}
                    iconName='rectangles'
                    tooltip='Cases (C)'
                />
                {/* Information */}
                <ButtonRound
                    href={informationUrl}
                    iconName='i-circle'
                    tooltip='Information (I)'
                />
                {/* Settings */}
                <ButtonRound
                    href={settingsUrl}
                    iconName='gear'
                    tooltip='Settings (S)'
                    tooltipPosition='right'
                />
            </Header>
        );
    }

    function renderName (name: string) {
        if (!name) {
            return null;
        }

        return (
            <Group
                alignment='center'
                ariaLive={true}
                title='Name'
                tooltip='Name to identify the case'
            >
                <Spoiler alignment='center' open={!settings.trainer.hideName}>
                    <Tag
                        color='grey-dark'
                        label={name}
                        size='large'
                    />
                </Spoiler>
            </Group>
        );
    }

    function renderPreview (caseId: string) {
        if (!trainerCases || trainerCases.length === 0) {
            throw new Error('Missing trainer cases.');
        }

        if (!caseId) {
            throw new Error('Missing case id.');
        }

        const source = getCaseImageSource(puzzleName, trainerId, caseId);

        return (
            <Group
                ariaLive={true}
                title='Preview'
                tooltip='Preview of the case'
            >
                <Spoiler alignment='center' open={!settings.trainer.hidePreview}>
                    <div className={`${className}__puzzle-preview`}>
                        <PuzzlePreview source={source} />
                    </div>
                    <p
                        className={`${className}__case-index`}
                        data-tooltip='Index of the case in the queue'
                        data-tooltip-position='center'
                        tabIndex={0}
                    >
                        ({activeCaseIndex + 1}/{trainerCases.length})
                    </p>
                </Spoiler>
            </Group>
        );
    }

    function renderStatus (status?: TTrainerCaseStatus) {
        if (typeof(status) !== 'number')  {
            return null;
        }

        return (
            <Group
                alignment='center'
                ariaLive={true}
                title='Status'
                tooltip='Status of the case'
            >
                <Tag
                    color={trainerCaseStatus[status].color}
                    label={trainerCaseStatus[status].label}
                    size='large'
                />
            </Group>
        );
    }

    function renderOrientation () {
        return (
            <Group
                alignment='center'
                ariaLive={true}
                title='Orientation'
                tooltip='Orientation in which to hold the puzzle'
            >
                <Orientation puzzleName={puzzleName} />
            </Group>
        );
    }

    function renderSetup (setup?: string) {
        if (!setup) {
            return null;
        }

        return (
            <Group
                alignment='left'
                ariaLive={true}
                title='Setup'
                tooltip='Algorithm to setup the case'
            >
                <Spoiler alignment='left' open={!settings.trainer.hideSetup}>
                    <Tag
                        color='grey'
                        label={setup}
                        largeLetterSpacing={true}
                        size='large'
                    />
                </Spoiler>
            </Group>
        );
    }

    function renderSolutions (solutions?: string[], favoriteSolution?: string) {
        let numberOfSolutions = solutions?.length || 0;

        if (favoriteSolution && !solutions?.includes(favoriteSolution)) {
            numberOfSolutions++;
        }

        if (numberOfSolutions === 0) {
            return null;
        }

        let groupTitle: string;
        if (numberOfSolutions > 1) {
            groupTitle = `Solutions (${numberOfSolutions})`;
        } else {
            groupTitle = `Solution (${numberOfSolutions})`;
        }

        const favoriteSolutionElement = favoriteSolution && (
            <li className={`${className}__solution`}>
                <Tag
                    color='grey-super-light'
                    label={favoriteSolution}
                    largeLetterSpacing={true}
                    size='large'
                    tooltip='Favorite solution'
                    tooltipPosition='center'
                />
            </li>
        );

        const solutionElements = solutions?.map((solution, index) => {
            if (solution !== favoriteSolution) {
                return (
                    <li className={`${className}__solution`} key={`solution-${index}`}>
                        <Tag
                            color='grey'
                            label={solution}
                            largeLetterSpacing={true}
                            size='large'
                        />
                    </li>
                );
            }
        });

        return (
            <Group
                alignment='left'
                ariaLive={true}
                title={groupTitle}
                tooltip='Algorithm(s) to solve the case'
            >
                <Spoiler open={!settings.trainer.hideSolutions}>
                    <ul className={`${className}__solutions`}>
                        {favoriteSolutionElement}
                        {solutionElements}
                    </ul>
                </Spoiler>
            </Group>
        );
    }

    function renderActions () {
        if (!activeCase) {
            return null;
        }

        const label = activeCase.favoriteSolution ? 'Edit Favorite Solution' : 'Add Favorite Solution';
        const tooltip = activeCase.favoriteSolution ? 'Edit favorite solution  (F)' : 'Add favorite solution  (F)';

        return (
            <List alignment='right'>
                <Button
                    callback={setFavoriteSolution}
                    label={label}
                    tooltip={tooltip}
                    tooltipPosition='right'
                />
            </List>
        );
    }

    function renderTimes (times?: TTrainerCaseTime[]) {
        let content: ReactNode;

        if (!times || times.length === 0) {
            content = (
                <Fragment>
                    <p className={`${className}__text`}>No recorded times yet.</p>
                    <List>
                        {renderAddTime()}
                    </List>
                </Fragment>
            );
        } else {
            content = (
                <div className={`${className}__times`}>
                    {/* Stats */}
                    {renderStats(times)}
                    {/* Chart */}
                    {renderChart(times)}
                    {/* List */}
                    {renderList(times)}
                </div>
            );
        }

        return (
            <Accordion
                openByDefault={settings.trainer.keepTimesOpen}
                title={`Times (${times?.length || 0})`}
            >
                {content}
            </Accordion>
        );
    }

    function renderStats (times?: TTrainerCaseTime[]) {
        if (!trainer) {
            throw new Error('Missing trainer.');
        }

        if (!activeCase || !times || times.length === 0) {
            return null;
        }

        const average = getAverageTime(times);
        const best = getBestTime(times);
        const worst = getWorstTime(times);
        const plusTwos = getPlusTwoTimes(times)?.length || 0;
        const dnfs = getDnfTimes(times)?.length || 0;
        const total = times.length;

        const nameElement = (
            <Info label='Name' value={activeCase.name} />
        );

        const averageElement = total > 1 && average ? (
            <Info color='blue' label='Average' value={formatTime(average)} />
        ) : null;

        const bestElement = total > 1 && best ? (
            <Info
                color='yellow'
                label='Best'
                value={formatTime(best.milliseconds) + (best.penalty === '+2' ? '+' : '')}
            />
        ) : null;

        const worstElement = total > 1 && worst ? (
            <Info
                label='Worst'
                value={formatTime(worst.milliseconds) + (worst.penalty === '+2' ? '+' : '')}
            />
        ) : null;

        const plusTwoElement = plusTwos ? (
            <Info color='orange' label='+2' value={plusTwos.toString()} />
        ) : null;

        const dnfElement = dnfs ? (
            <Info color='red' label='DNF' value={dnfs.toString()} />
        ) : null;

        const totalElement = (
            <Info label='Total' value={total.toString()} />
        );

        return (
            <Group title='Stats' tooltip='Time statistics'>
                <List gap={15}>
                    {nameElement}
                    {averageElement}
                    {bestElement}
                    {worstElement}
                </List>
                <List gap={15}>
                    {totalElement}
                    {plusTwoElement}
                    {dnfElement}
                </List>
            </Group>
        );
    }

    function renderChart (times?: TTrainerCaseTime[]) {
        if (!times || times.length === 0) {
            return null;
        }

        const labels: number[] = [];
        const singles: number[] = [];
        const singlesBackgroundColor: string[] = [];
        const singlesRadius: number[] = [];
        const singlesStyle: string[] = [];
        const averages: { x: number, y: number }[] = [];
        const bests: { x: number, y: number }[] = [];
        let lastBest: number;

        // Singles
        times.forEach((time, index) => {
            switch (time.penalty) {
                case '+2':
                    labels.push(index + 1);
                    singles.push(time.milliseconds + 2000);
                    singlesBackgroundColor.push(colors.orange);
                    singlesRadius.push(7);
                    singlesStyle.push('rect');
                    break;
                case 'dnf':
                    break;
                default:
                    labels.push(index + 1);
                    singles.push(time.milliseconds);
                    singlesBackgroundColor.push(colors.green);
                    singlesRadius.push(5);
                    singlesStyle.push('circle');
                    break;
            }
        });

        if (singles.length > 1) {
            singles.forEach((single, index) => {
                if (index > 0) {
                    // Averages
                    const subset = singles.slice(0, index + 1);
                    let sum = 0;

                    subset.forEach((subsetSingle) => {
                        sum += subsetSingle;
                    });

                    averages.push({ x: labels[index], y: sum / subset.length });
                }

                // Bests
                if (index === 0 || single < lastBest) {
                    bests.push({ x: labels[index], y: single });
                    lastBest = single;
                }
            });
        }

        const data: ChartData<'line'> = {
            datasets: [],
            labels
        };

        if (bests.length > 0) {
            data.datasets.push(
                {
                    borderColor: colors.yellow,
                    borderDash: [5, 5],
                    data: bests,
                    label: 'Best',
                    pointBackgroundColor: colors.yellow,
                    pointRadius: 5
                }
            );
        }

        if (averages.length > 0) {
            data.datasets.push(
                {
                    borderColor: colors.blue,
                    data: averages,
                    label: 'Average',
                    pointBackgroundColor: colors.blue,
                    pointRadius: 5
                }
            );
        }

        if (singles.length > 0) {
            data.datasets.push(
                {
                    borderColor: colors.greySuperLight,
                    borderDash: [20, 5],
                    data: singles,
                    label: 'Single',
                    pointBackgroundColor: singlesBackgroundColor,
                    pointRadius: singlesRadius,
                    pointStyle: singlesStyle
                }
            );
        }

        return (
            <Group title='Chart' tooltip='Time chart'>
                <ChartLine data={data} />
            </Group>
        );
    }

    function renderList (times?: TTrainerCaseTime[]) {
        if (!times || times.length === 0) {
            return null;
        }

        const listElements: ReactNode[] = [];

        for (let i = times.length - 1; i >= 0; i--) {
            listElements.push(
                <li key={`time-${i}`}>
                    <Time
                        callbackPenalty={setTimePenalty}
                        callbackRemove={removeTime}
                        index={i}
                        last={i === times.length - 1}
                        time={times[i]}
                    />
                </li>
            );
        }

        return (
            <Group title={`List (${times.length})`} tooltip='Time list'>
                <List>
                    {renderAddTime()}
                    <Button
                        callback={removeAllTimes}
                        color='red'
                        iconName='trash'
                        label='Remove All'
                        tooltip='Remove all times'
                    />
                </List>
                <ul className={`${className}__time-list`}>
                    {listElements}
                </ul>
            </Group>
        );
    }

    function renderAddTime () {
        return (
            <Button
                callback={addTimeManually}
                iconName='plus'
                label='Add time'
                tooltip='Add time manually (A)'
            />
        );
    }

    function renderControls (status?: TTrainerCaseStatus) {
        const buttonStatus = typeof(status) === 'number' ? (
            <ButtonStatus
                callback={changeStatus}
                status={status}
            />
        ) : null;

        return (
            <div className={`${className}__controls`}>
                <div className={`${className}__controls-background`} />
                {/* Previous */}
                <ButtonRound
                    callback={previousCase}
                    iconName='arrow-left'
                    tooltip='Previous case (Left Arrow)'
                />
                {/* Timer */}
                <ButtonTimer
                    running={running}
                    startCallback={startTimer}
                    stopCallback={stopTimer}
                />
                {/* Status */}
                {buttonStatus}
                {/* Next */}
                <ButtonRound
                    callback={nextCase}
                    iconName='arrow-right'
                    tooltip='Next case (Right Arrow)'
                    tooltipPosition='right'
                />
            </div>
        );
    }

    function renderTimer () {
        const value = settings.trainer.hideTimerValue ? '-.---' : formatTime(milliseconds);

        return (
            <button
                aria-label='Stop timer (Space)'
                className={`${className}__timer`}
                onClick={stopTimer}
                ref={timerRef}
            >
                <span className={`${className}__timer-value`}>{value}</span>
                <span className={`${className}__timer-help`}>Press (Spacebar) or click anywhere to stop the timer.</span>
            </button>
        );
    }

    function renderContent () {
        if (!trainer) {
            throw new Error('Missing trainer.');
        }

        if (!activeCase) {
            throw new Error('Missing active case.');
        }

        return (
            <div className={`${className}__content`}>
                <div className={`${className}__columns`}>
                    <div className={`${className}__column`}>
                        {/* Name */}
                        {renderName(activeCase.name)}
                        {/* Preview */}
                        {renderPreview(activeCase.id)}
                    </div>
                    <div className={`${className}__column`}>
                        {/* Status */}
                        {renderStatus(activeCase.status)}
                        {/* Orientation */}
                        {renderOrientation()}
                        {/* Setup */}
                        {renderSetup(activeCase.setup)}
                        {/* Solutions */}
                        {renderSolutions(activeCase.solutions, activeCase.favoriteSolution)}
                    </div>
                </div>
                {/* Actions */}
                {renderActions()}
                {/* Times */}
                {renderTimes(activeCase.times)}
                {/* Controls */}
                {renderControls(activeCase.status)}
            </div>
        );
    }

    return (
        <Page loading={loading} title={`Trainer - ${trainer?.name}`}>
            <div className={className}>
                {renderHeader()}
                {
                    trainerCases.length === 0 ? (
                        <Group title='No selected case'>
                            <p>Please select at least one case.</p>
                        </Group>
                    ) : renderContent()
                }
                { running && renderTimer()}
            </div>
        </Page>
    );
}