import React from 'react';
import { TTrainerCaseStatus } from '../types';
import { trainerCaseStatus } from '../utils';
import { Icon } from './Icon';

type TProps = {
    callback?: (...args: unknown[]) => unknown;
    status: TTrainerCaseStatus;
};

export function ButtonStatus ({ callback, status }: TProps) {
    const className = 'button-status';
    const classNames = [className];

    let iconName: string;
    let tooltip: string;

    switch (status) {
        case 0:
            classNames.push(`${className}--color-${trainerCaseStatus[1].color}`);
            iconName = trainerCaseStatus[1].iconName;
            tooltip = `Change status to "${trainerCaseStatus[1].label}" (E)`;
            break;
        case 1:
            classNames.push(`${className}--color-${trainerCaseStatus[2].color}`);
            iconName = trainerCaseStatus[2].iconName;
            tooltip = `Change status to "${trainerCaseStatus[2].label}" (E)`;
            break;
        case 2:
            classNames.push(`${className}--color-${trainerCaseStatus[0].color}`);
            iconName = trainerCaseStatus[0].iconName;
            tooltip = `Change status to "${trainerCaseStatus[0].label}" (E)`;
            break;
    }


    return (
        <button
            aria-label={tooltip}
            className={classNames.join(' ')}
            data-tooltip={tooltip}
            data-tooltip-position='center'
            onClick={callback}
            type='button'
        >
            <Icon name={iconName} parentClass={`${className}__icon`} />
        </button>
    );
}