import React, { ReactNode } from 'react';
import { createRoot } from 'react-dom/client';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import { ErrorBoundary } from './components/ErrorBoundary';
import { Cases } from './pages/Cases';
import { Faq } from './pages/Faq';
import { Home } from './pages/Home';
import { Information } from './pages/Information';
import { PageNotFound } from './pages/PageNotFound';
import { Settings } from './pages/Settings';
import { Trainer } from './pages/Trainer';

const trainers = {
    '2x2x2': [
        'notation',
        'oll',
        'pll',
        'pbl'
    ],
    '3x3x3': [
        'notation',
        'twoLookOll',
        'twoLookPll',
        'f2l',
        'oll',
        'pll'
    ]
};

function wrapPage (page: ReactNode) {
    return (
        <ErrorBoundary>
            {page}
        </ErrorBoundary>
    );
}

const rootElement = document.getElementById('root');

if (rootElement) {
    const root = createRoot(rootElement);

    const trainerRoutes: ReactNode[] = [];
    Object.keys(trainers).forEach((puzzleName) => {
        trainers[puzzleName].forEach((trainerId) => {
            trainerRoutes.push(
                <Route path={`/trainer/${puzzleName}/${trainerId}`} key={`trainer-${puzzleName}-${trainerId}`}>
                    <Route index element={wrapPage(<Trainer />)} />
                    <Route path='cases' element={wrapPage(<Cases />)} />
                    <Route path='information' element={wrapPage(<Information />)} />
                    <Route path='settings' element={wrapPage(<Settings />)} />
                </Route>
            );
        });
    });

    root.render(
        <BrowserRouter>
            <Routes>
                <Route path='/' element={wrapPage(<Home />)} />
                {trainerRoutes}
                <Route path='/faq' element={wrapPage(<Faq />)} />
                <Route path='/settings' element={wrapPage(<Settings />)} />
                <Route path='*' element={wrapPage(<PageNotFound />)} />
            </Routes>
        </BrowserRouter>
    );
} else {
    console.error('Could not find root element.');
}