import React from 'react';
import { TTooltipPosition } from '../types';

type TProps = {
    color?: 'blue' | 'green' | 'grey-dark' | 'grey' | 'grey-super-light' | 'orange' | 'red' | 'white';
    label: string;
    largeLetterSpacing?: boolean;
    size?: 'medium' | 'large';
    tooltip?: string;
    tooltipPosition?: TTooltipPosition;
};

export function Tag ({ color = 'grey', label, largeLetterSpacing, size = 'medium', tooltip, tooltipPosition }: TProps) {
    const className = 'tag';
    const classNames = [className];

    classNames.push(`${className}--color-${color}`);
    classNames.push(`${className}--size-${size}`);

    if (largeLetterSpacing) {
        classNames.push(`${className}--large-letter-spacing`);
    }

    return (
        <div
            className={classNames.join(' ')}
            data-tooltip={tooltip}
            data-tooltip-position={tooltipPosition}
            tabIndex={tooltip ? 0 : undefined}
        >
            <p className={`${className}__label`}>{label}</p>
        </div>
    );
}