import React, { ChangeEvent, useState } from 'react';
import { getRandomInteger } from '../utils';

type TProps = {
    callback: (event: ChangeEvent<HTMLInputElement>) => unknown;
    defaultChecked: boolean;
    description: string;
    label: string;
};

export function Switch ({ callback, defaultChecked, description, label }: TProps) {
    const className = 'switch';

    const [checked, setChecked] = useState<boolean>(defaultChecked || false);

    const inputId = `switch-${getRandomInteger(100000, 999999)}`;
    const descriptionId = `switch-description-${getRandomInteger(100000, 999999)}`;

    function changeHandler (event: ChangeEvent<HTMLInputElement>) {
        setChecked(event.target.checked);
        callback(event);
    }

    return (
        <div className={className}>
            <p className={`${className}__description`} id={descriptionId}>{description}</p>
            <div className={`${className}__field`}>
                <input
                    aria-describedby={descriptionId}
                    className={`${className}__input`}
                    id={inputId}
                    onChange={changeHandler}
                    type='checkbox'
                    checked={checked}
                />
                <label className={`${className}__label`} htmlFor={inputId}>{label}</label>
            </div>
        </div>
    );
}