import React from 'react';
import { TTrainerCaseTime, TTrainerCaseTimePenaly } from '../types';
import { formatTime } from '../utils';
import { Icon } from './Icon';

type TProps = {
    callbackPenalty: (index: number, penalty: TTrainerCaseTimePenaly) => void;
    callbackRemove: (index: number) => void;
    index: number;
    last: boolean;
    time: TTrainerCaseTime;
};

export function Time ({ callbackPenalty, callbackRemove, index, last, time }: TProps) {
    const className = 'time';

    const { penalty, milliseconds } = time;

    let value = formatTime(milliseconds);
    let buttonCallback: () => void;
    let buttonClass: string;
    let buttonIconName: string;
    let buttonTooltip: string;

    switch (penalty) {
        case '+2':
            value += '+';
            buttonCallback = () => callbackPenalty(index, 'dnf');
            buttonClass = `${className}__button--plus-two`;
            buttonIconName = 'plus-two';
            buttonTooltip = `Set penalty to DNF${last ? ' (P)' : ''}`;
            break;
        case 'dnf':
            value += ' (DNF)';
            buttonCallback = () => callbackPenalty(index, null);
            buttonClass = `${className}__button--dnf`;
            buttonIconName = 'x';
            buttonTooltip = `Remove penalty${last ? ' (P)' : ''}`;
            break;
        default:
            buttonCallback = () => callbackPenalty(index, '+2');
            buttonClass = `${className}__button--valid`;
            buttonIconName = 'check';
            buttonTooltip = `Set penalty to +2${last ? ' (P)' : ''}`;
            break;
    }

    return (
        <div className={className}>
            <p
                className={`${className}__index`}
                data-tooltip='Index'
                data-tooltip-position='center'
            >
                {index + 1}
            </p>
            <p
                className={`${className}__value`}
                data-tooltip='Time'
                data-tooltip-position='center'
            >
                {value}
            </p>
            <button
                className={`${className}__button ${buttonClass}`}
                onClick={buttonCallback}
                data-tooltip={buttonTooltip}
                data-tooltip-position='center'
            >
                <Icon name={buttonIconName} parentClass={`${className}__icon`} />
            </button>
            <button
                className={`${className}__button ${className}__button-remove`}
                onClick={() => callbackRemove(index)}
                data-tooltip={`Remove${last ? ' (Delete)' : ''}`}
                data-tooltip-position='center'
            >
                <Icon name='trash' parentClass={`${className}__icon`} />
            </button>
        </div>
    );
}