import React from 'react';
import { Accordion } from '../components/Accordion';
import { Button } from '../components/Button';
import { ButtonRound } from '../components/ButtonRound';
import { ColorSchemePicker } from '../components/ColorSchemePicker';
import { Group } from '../components/Group';
import { Header } from '../components/Header';
import { List } from '../components/List';
import { Page } from '../components/Page';
import { Switch } from '../components/Switch';
import { useEventListener } from '../hooks';
import { storeManager } from '../StoreManager';
import { TPuzzleName } from '../types';

export function Settings () {
    const className = 'settings';

    const pathname = window.location.pathname.split('/');
    const puzzleName = pathname[2] as TPuzzleName;
    const trainerId = pathname[3];

    document.title = 'SST - Settings';

    const homeUrl = '/';
    const trainerUrl = `/trainer/${puzzleName}/${trainerId}`;

    const settings = storeManager.getSettings();

    useEventListener(document, 'keydown', keydownHandler);

    function keydownHandler (event: KeyboardEvent) {
        if (
            event.altKey
            || event.ctrlKey
            || event.metaKey
            || event.shiftKey
        ) {
            return;
        }

        // H
        if (event.code === 'KeyH') {
            event.preventDefault();
            window.location.href = homeUrl;
        }

        // T
        if (puzzleName && trainerId && event.code === 'KeyT') {
            event.preventDefault();
            window.location.href = trainerUrl;
        }
    }

    function exportBackup () {
        // Create a file containing the store
        const fileContent = JSON.stringify(storeManager.store);
        const file = new Blob([fileContent], { type: 'application/json' });

        // Create a link to download the file
        const linkElement = document.createElement('a');
        linkElement.href = URL.createObjectURL(file);
        const date = new Date();
        linkElement.download = `${date.toISOString()}.json`;

        // Programatically click the link
        linkElement.click();
    }

    function importBackup () {
        const confirm = window.confirm('Are you sure you want to import a backup? This will overwrite all your data, it cannot be undone.');

        if (confirm) {
            // Create an input upload the file
            const inputElement = document.createElement('input');
            inputElement.type = 'file';

            // Add event listener to get the file
            inputElement.addEventListener('change', () => {
                // Get file text
                inputElement.files?.[0].text().then((text) => {
                    try {
                        // Parse text
                        const newStore = JSON.parse(text);
                        // Update store
                        storeManager.setStore(newStore);

                        window.alert('Backup imported successfully.');
                    } catch (error) {
                        console.error(error);
                        window.alert('Error: Corrupted backpup.');
                    }
                });
            });

            // Programatically click the input
            inputElement.click();
        }
    }

    function resetApp () {
        const confirm = window.confirm('Are you sure you want to reset the app? All you data will be lost. This cannot be undone.');

        if (confirm) {
            storeManager.resetStore();
        }
    }

    function renderHeader () {
        const trainerButton = puzzleName && trainerId && (
            <ButtonRound
                href={trainerUrl}
                iconName='cube'
                tooltip='Trainer (T)'
            />
        );

        return (
            <Header>
                {/* Home */}
                <ButtonRound
                    href={homeUrl}
                    iconName='house'
                    tooltip='Home (H)'
                />
                {/* Trainer */}
                {trainerButton}
            </Header>
        );
    }

    function renderTrainer () {
        return (
            <Group title='Trainer'>
                <Switch
                    description='Randomize case order'
                    callback={(event) => storeManager.setTrainerSetting('randomizeCaseOrder', event.target.checked)}
                    defaultChecked={settings.trainer.randomizeCaseOrder}
                    label='Randomise the order of cases in the queue'
                />
                <Switch
                    description='Hide name'
                    callback={(event) => storeManager.setTrainerSetting('hideName', event.target.checked)}
                    defaultChecked={settings.trainer.hideName}
                    label='Hide the name of the case'
                />
                <Switch
                    description='Hide preview'
                    callback={(event) => storeManager.setTrainerSetting('hidePreview', event.target.checked)}
                    defaultChecked={settings.trainer.hidePreview}
                    label='Hide the preview of the case'
                />
                <Switch
                    description='Hide setup'
                    callback={(event) => storeManager.setTrainerSetting('hideSetup', event.target.checked)}
                    defaultChecked={settings.trainer.hideSetup}
                    label='Hide the setup of the case'
                />
                <Switch
                    description='Hide solution(s)'
                    callback={(event) => storeManager.setTrainerSetting('hideSolutions', event.target.checked)}
                    defaultChecked={settings.trainer.hideSolutions}
                    label='Hide the solution(s) of the case'
                />
            </Group>
        );
    }

    function renderTimer () {
        return (
            <Group title='Timer'>
                <Switch
                    description='Keep times open'
                    callback={(event) => storeManager.setTrainerSetting('keepTimesOpen', event.target.checked)}
                    defaultChecked={settings.trainer.keepTimesOpen}
                    label='Keep the times accordion open'
                />
                <Switch
                    description='Hide timer value'
                    callback={(event) => storeManager.setTrainerSetting('hideTimerValue', event.target.checked)}
                    defaultChecked={settings.trainer.hideTimerValue}
                    label='Hide the time while the timer is running'
                />
            </Group>
        );
    }

    function render2x2x2 () {
        return (
            <Accordion title='2x2x2'>
                <Switch
                    description='Color neutral'
                    callback={(event) => storeManager.setPuzzleSetting('2x2x2', 'colorNeutral', event.target.checked)}
                    defaultChecked={settings.puzzles['2x2x2'].colorNeutral}
                    label='Solve this puzzle from any side'
                />
                <ColorSchemePicker puzzleName={'2x2x2'} />
            </Accordion>
        );
    }

    function render3x3x3 () {
        return (
            <Accordion title='3x3x3'>
                <Switch
                    description='Color neutral'
                    callback={(event) => storeManager.setPuzzleSetting('3x3x3', 'colorNeutral', event.target.checked)}
                    defaultChecked={settings.puzzles['3x3x3'].colorNeutral}
                    label='Solve this puzzle from any side'
                />
                <ColorSchemePicker puzzleName={'3x3x3'} />
            </Accordion>
        );
    }

    function renderBackup () {
        return (
            <Group alignment='left' title='Backups'>
                <List>
                    <Button
                        callback={importBackup}
                        iconName='arrow-half-square-right'
                        label='Import'
                        tooltip='Import backup'
                    />
                    <Button
                        callback={exportBackup}
                        iconName='arrow-half-square-left'
                        label='Export'
                        tooltip='Export backup'
                    />
                </List>
            </Group>
        );
    }

    function renderDangerZone () {
        return (
            <Group alignment='left' title='Danger zone'>
                <Button
                    callback={resetApp}
                    color='red'
                    iconName='trash'
                    label='Reset App'
                />
            </Group>
        );
    }

    return (
        <Page title='Settings'>
            <div className={className}>
                {/* Header */}
                {renderHeader()}
                <div className={`${className}__content`}>
                    {renderTrainer()}
                    {renderTimer()}
                    {render2x2x2()}
                    {render3x3x3()}
                    {renderBackup()}
                    {renderDangerZone()}
                </div>
            </div>
        </Page>
    );
}