import React, { useEffect } from 'react';
import { version } from '../../../package.json';
import { Accordion } from '../components/Accordion';
import { Button } from '../components/Button';
import { ButtonRound } from '../components/ButtonRound';
import { CasePreview } from '../components/CasePreview';
import { Group } from '../components/Group';
import { Header } from '../components/Header';
import { Page } from '../components/Page';
import { useEventListener } from '../hooks';
import { storeManager } from '../StoreManager';
import { TPuzzleName } from '../types';
import { updateColorSchemeCustomProperties } from '../utils';

type THomeTrainer = {
    id: string;
    name: string
    caseId: string;
};

type THomeTrainers = {
    [key in TPuzzleName]: {
        [key: string]: {
            [key: string]: THomeTrainer
        }
    }
};

export function Home () {
    const className = 'home';

    document.title = 'Speed Solving Trainer';

    const faqUrl = '/faq';
    const settingsUrl = '/settings';

    const trainers: THomeTrainers = {
        '2x2x2': {
            Beginner: {
                notation: {
                    id: 'notation',
                    name: 'Notation',
                    caseId: '1'
                },
                oll: {
                    id: 'oll',
                    name: 'OLL',
                    caseId: '3'
                },
                pll: {
                    id: 'pll',
                    name: 'PLL',
                    caseId: '0'
                }
            },
            Advanced: {
                pbl: {
                    id: 'pbl',
                    name: 'PBL',
                    caseId: '2'
                }
            }
        },
        '3x3x3': {
            Beginner: {
                notation: {
                    id: 'notation',
                    name: 'Notation',
                    caseId: '1'
                },
                twoLookOll: {
                    id: 'twoLookOll',
                    name: '2 Look OLL',
                    caseId: '3'
                },
                twoLookPll: {
                    id: 'twoLookPll',
                    name: '2 Look PLL',
                    caseId: '2'
                }
            },
            CFOP: {
                f2l: {
                    id: 'f2l',
                    name: 'F2L',
                    caseId: '0'
                },
                oll: {
                    id: 'oll',
                    name: 'OLL',
                    caseId: '49'
                },
                pll: {
                    id: 'pll',
                    name: 'PLL',
                    caseId: '6'
                }
            }
        }
    };

    useEffect(() => {
        updateColorSchemeCustomProperties(storeManager.getColorScheme('2x2x2'));
        updateColorSchemeCustomProperties(storeManager.getColorScheme('3x3x3'));
    }, []);

    useEventListener(document, 'keydown', keydownHandler);

    function keydownHandler (event: KeyboardEvent) {
        if (
            event.altKey
            || event.ctrlKey
            || event.metaKey
            || event.shiftKey
        ) {
            return;
        }

        // F
        if (event.code === 'KeyF') {
            event.preventDefault();
            window.location.href = faqUrl;
        }

        // S
        if (event.code === 'KeyS') {
            event.preventDefault();
            window.location.href = settingsUrl;
        }
    }

    function renderHeader () {
        return (
            <Header marginLeftIndex={0}>
                {/* Faq */}
                <ButtonRound
                    href={faqUrl}
                    iconName='question-mark-circle'
                    tooltip='FAQ (F)'
                    tooltipPosition='right'
                />
                {/* Settings */}
                <ButtonRound
                    href={settingsUrl}
                    iconName='gear'
                    tooltip='Settings (S)'
                    tooltipPosition='right'
                />
            </Header>
        );
    }

    function renderAccordion (puzzleName: TPuzzleName) {
        if (!puzzleName) {
            throw new Error('Missing puzzle name.');
        }

        return puzzleName ? (
            <Accordion key={`group-${puzzleName}`} title={puzzleName}>
                {
                    Object.keys(trainers[puzzleName]).map((method) => (
                        <Group title={method} key={`group-${puzzleName}-${method}`}>
                            <ul className={`${className}__list`}>
                                {
                                    Object.keys(trainers[puzzleName][method]).map((trainerId) => (
                                        <li className={`${className}__item`} key={`item-${trainerId}`}>
                                            {renderTrainer(puzzleName, trainers[puzzleName][method][trainerId])}
                                        </li>
                                    ))
                                }
                            </ul>
                        </Group>
                    ))
                }
            </Accordion>
        ) : null;
    }

    function renderTrainer (puzzleName: TPuzzleName, trainer: THomeTrainer) {
        if (!puzzleName) {
            throw new Error('Missing puzzle name.');
        }

        if (!trainer.id) {
            throw new Error('Missing trainer id.');
        }

        if (!trainer.name) {
            throw new Error('Missing trainer name.');
        }

        return (
            <CasePreview
                href={`/trainer/${puzzleName}/${trainer.id}`}
                name={trainer.name}
                source={`/public/trainers/${puzzleName}/${trainer.id}/images/${trainer.caseId}.svg`}
                tooltip={`${puzzleName} - ${trainer.name}`}
            />
        );
    }

    function renderFooter () {
        const iconElement = <span className={`${className}__heart`} aria-label='Love'></span>;
        const thomasElement = <a className={`${className}__link`} href='https://github.com/ThomasHenrissat' target='_blank'>Thomas Henrissat</a>;
        const julianElement = <a className={`${className}__link`} href='https://github.com/j-lohuis' target='_blank'>Julian Lohuis</a>;

        return (
            <footer className={`${className}__footer`}>
                <p>Made with {iconElement} by {thomasElement}</p>
                <p>Special thanks to {julianElement}</p>
            </footer>
        );
    }

    return (
        <Page>
            <div className={className}>
                {/* Header */}
                {renderHeader()}
                {/* Head */}
                <div className={`${className}__head`}>
                    <img className={`${className}__logo`} src={'/public/assets/images/logo.svg'} alt="Logo" />
                    <h1 className={`${className}__title`}>Speed Solving Trainer</h1>
                    <p className={`${className}__version`}>Version {version}</p>
                </div>
                {/* Trainers */}
                <div className={`${className}__section`}>
                    <h2 className={`${className}__section-title`}>Trainers</h2>
                    {
                        Object.keys(trainers).map((puzzleName) => (
                            renderAccordion(puzzleName as TPuzzleName)
                        ))
                    }
                </div>
                {/* Community */}
                <div className={`${className}__section`}>
                    <h2 className={`${className}__section-title`}>Join the community</h2>
                    <Button
                        color='discord'
                        href='https://discord.gg/F6eaxK88sx'
                        iconName='discord'
                        label='Discord'
                        target='_blank'
                        tooltip='Join Discord server'
                        tooltipPosition='center'
                    />
                </div>
                {/* Footer */}
                {renderFooter()}
            </div>
        </Page>
    );
}