import React, { ChangeEvent, useState } from 'react';
import { storeManager } from '../StoreManager';
import { TPuzzleName } from '../types';
import { getHexColorLonghand } from '../utils';
import { Button } from './Button';

type TProps = {
    puzzleName: TPuzzleName;
};

export function ColorSchemePicker ({ puzzleName }: TProps) {
    const className = 'color-scheme-picker';

    const [colorScheme, setColorScheme] = useState(storeManager.getColorScheme(puzzleName));

    function changeHandler (event: ChangeEvent<HTMLInputElement>, colorName: string) {
        // Create updated color scheme
        const newColorScheme = { ...colorScheme, [colorName]: event.target.value };

        // Update store
        storeManager.setColorScheme(puzzleName, newColorScheme);

        // Update state
        setColorScheme(storeManager.getColorScheme(puzzleName));
    }

    function reset () {
        const confirm = window.confirm('Are you sure you want to reset this color scheme to the default colors?');

        if (confirm) {
            // Update store
            storeManager.resetColorScheme(puzzleName);

            // Update state
            setColorScheme(storeManager.getColorScheme(puzzleName));
        }
    }

    return (
        <div className={className}>
            <p className={`${className}__label`}>Color Scheme</p>
            <ul className={`${className}__list`}>
                {
                    Object.keys(colorScheme).map((colorName, index) => (
                        <li
                            className={`${className}__item`}
                            key={`color-scheme-${puzzleName}-${index}`}
                            data-tooltip='Change color'
                            data-tooltip-position='center'
                        >
                            <input
                                className={`${className}__input`}
                                onChange={(event) => changeHandler(event, colorName)}
                                type="color"
                                value={getHexColorLonghand(colorScheme[colorName])}
                            />
                        </li>
                    ))
                }
            </ul>
            <Button
                callback={reset}
                color='red'
                iconName='trash'
                label='Reset Colors'
            />
        </div>
    );
}