import { CategoryScale, Chart, ChartConfiguration, ChartData, Legend, LinearScale, LineController, LineElement, PointElement, Tooltip } from 'chart.js';
import isEqual from 'lodash/isEqual';
import React, { useEffect, useRef, useState } from 'react';
import { colors } from '../colors';
import { usePrevious } from '../hooks';
import { formatTime } from '../utils';
import { Button } from './Button';
import { List } from './List';

Chart.register(Legend, LineController, LineElement, PointElement, CategoryScale, LinearScale, Tooltip);

type TProps = {
    data: ChartData<'line'>;
};

export function ChartLine ({ data }: TProps) {
    const className = 'chart-line';

    const [chart, setChart] = useState<Chart<'line'>>();
    const [chartWidth, setChartWidth] = useState(100);

    const chartRef = useRef<HTMLCanvasElement>(null);
    const previousData: ChartData<'line'> = usePrevious(data);

    const zoomFactor = 100;

    Chart.defaults.font.family = 'Roboto';
    Chart.defaults.font.size = 14;
    Chart.defaults.font.weight = 500;

    const config: ChartConfiguration<'line'> = {
        type: 'line',
        options: {
            color: colors.white,
            elements: {
                point: {
                    borderWidth: 0
                }
            },
            maintainAspectRatio: false,
            plugins: {
                legend: {
                    labels: {
                        borderRadius: 2,
                        boxHeight: 15,
                        boxWidth: 15,
                        useBorderRadius: true
                    }
                },
                tooltip: {
                    callbacks: {
                        label: (context) => {
                            return formatTime(Number(context.parsed.y));
                        }
                    }
                }
            },
            scales: {
                x: {
                    grid: {
                        color: colors.grey
                    },
                    ticks: {
                        color: colors.white
                    }
                },
                y: {
                    grid: {
                        color: colors.grey
                    },
                    ticks: {
                        callback: (value) => formatTime(Number(value)),
                        color: colors.white
                    }
                }
            }
        },
        data
    };

    useEffect(() => {
        if (!chartRef.current) {
            return;
        }

        setChart(new Chart(chartRef.current, config));
    }, []);

    useEffect(() => {
        if (!chart) {
            return;
        }

        if (!isEqual(data, previousData)) {
            chart.data = data;
            chart.update();
        }
    }, [data]);

    function zoomIn () {
        if (chartWidth < 900) {
            setChartWidth(chartWidth + zoomFactor);
        }
    }

    function zoomOut () {
        if (chartWidth > 100) {
            setChartWidth(chartWidth - zoomFactor);
        }
    }

    function zoomReset () {
        setChartWidth(100);
    }

    return (
        <div className={className} aria-hidden='true'>
            <List>
                <Button
                    callback={zoomIn}
                    iconName='magnifier-plus'
                    label='Zoom In'
                />
                <Button
                    callback={zoomOut}
                    iconName='magnifier-minus'
                    label='Zoom Out'
                />
                <Button
                    callback={zoomReset}
                    iconName='square-split'
                    label='Reset Zoom'
                />
            </List>
            <div className={`${className}__zoom-container`}>
                <div className={`${className}__chart-container`}>
                    <div className={`${className}__chart`} style={{ width: chartWidth + '%' }}>
                        <canvas ref={chartRef} />
                    </div>
                </div>
            </div>
        </div>
    );
}